(function () {
    'use strict';

    angular
        .module('donna', ['ngAnimate', 'ui.router', 'oitozero.ngSweetAlert', 'luegg.directives', 'btford.socket-io',
            'angularUtils.directives.dirPagination', 'akoenig.deckgrid'])

        .run(function ($rootScope, $state, Auth) {
            $rootScope.$on('$stateChangeStart', function (event, toState) {
                if (!Auth.authorize(toState.data.access)) {
                    event.preventDefault();

                    $state.go('anon.login');
                }
            });
        });

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .directive('photoGallery', photoGallery);


    function photoGallery() {
        function link(scope, element, attrs) {
            jQuery(function ($) {
                $( '.swipebox' ).swipebox();
            });

            scope.$watch('photos', function (newVal, oldVal) {
                if (newVal) {
                    scope.gallery = newVal;
                    scope.delete = scope.$parent.galleryCtrl.destroyFile;
                    //console.log(scope.delete);
                }
            });
        }

        return {
            link: link,
            restrict: 'E',
            templateUrl: 'app/components/gallery/directive/gallery.html',
            transclude: true,
            scope: {
                model: '=photoGallery',
                photos: '='
            }
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('donna')
        .factory('User', User);


    function User ($http, API) {
        return {
            getOne: function (id) {
                return $http.get(API.URL + '/api/user/show/' + id);
            },

            getUsersList: function () {
                return $http.post(API.URL + '/api/users');
            },

            create: function (data) {
                return $http.post(API.URL + '/api/user/create', data);
            },

            update: function (id, data) {
                return $http.post(API.URL + '/api/user/update/' + id, data);
            },

            destroy: function (id) {
                return $http.post(API.URL + '/api/user/destroy/' + id);
            },

            getRoles: function () {
                return $http.get(API.URL + '/api/roles');
            }
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('donna')
        .controller('UserController', UserController);

    /** @ngInject */
    function UserController($state, User, SweetAlert) {
        var vm = this;
        var stateMethod = $state.current.method;
        vm.roles = [];

        User.getRoles().success(function (roles) {
            vm.roles = roles;

            // Create
            if (stateMethod == 'create') {
                vm.user = {};
            }

            // Update
            if (stateMethod == 'update') {
                vm.user = {};

                User.getOne($state.params.id).success(function (data) {
                    vm.user = data;
                }).error(function (err) {
                    SweetAlert.swal(err.error, err.summary, 'error');
                });
            }

        }).error(function (err) {
            SweetAlert.swal(err.error, err.summary, 'error');
        });


        // List
        if (stateMethod == 'list') {
            vm.users = [];

            User.getUsersList().success(function (data) {
                vm.users = data;
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }


        vm.save = function () {
            var action = (stateMethod == 'update') ? User.update($state.params.id, vm.user) : User.create(vm.user);

            action.success(function (user) {
                SweetAlert.swal({
                    title: 'Success',
                    text: 'Data successfully saved',
                    timer: 1000,
                    showConfirmButton: false,
                    type: 'success'
                });

                $state.go('user.users');

            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        };


        vm.destroy = function (id) {
            User.destroy(id).success(function () {
                vm.users = _.reject(vm.users, function (user) {
                    return id == user.id;
                });
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }
    }

})();
(function () {
    'use strict';

    angular
        .module('donna')
        .factory('Push', Push);


    function Push($http, API) {
        return {
            send: function (text) {
                return $http.post(API.URL + '/api/push/send', {text: text});
            }
        }
    }

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .controller('PushController', PushController);

    /** @ngInject */
    function PushController(Push, SweetAlert) {
        var vm = this;
        vm.text = '';

        vm.send = function () {
            Push.send(vm.text).success(function (data) {
                console.log(data);
                vm.text = '';
                SweetAlert.swal({
                    title: 'Success',
                    text: 'Pushes successfully sent',
                    timer: 2000,
                    showConfirmButton: false,
                    type: 'success'
                });
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }
    }


})();

(function() {
    'use strict';

    angular
        .module('donna')
        .directive('sidebar', sidebar);

    /** @ngInject */
    function sidebar() {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/sidebar/sidebar.html',
            controller: 'SidebarController',
            controllerAs: 'sidebarCtrl',
            bindToController: true
        };

        return directive;
    }

})();

(function() {
    'use strict';

    angular
        .module('donna')
        .controller('SidebarController', SidebarController);

    /** @ngInject */
    function SidebarController($location, Auth) {
        var vm = this;

        vm.isActive = function (route) {
            return route == $location.$$path;
        };
    }

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .factory('News', News);


    function News($http, API) {
        return {
            getNewsList: function () {
                return $http.get(API.URL + '/api/news');
            },

            destroyNews: function (id) {
                return $http.post(API.URL + '/api/news/destroy/' + id);
            },

            getOne: function (id) {
                return $http.get(API.URL + '/api/news/show/' + id);
            },

            create: function (data) {
                return data.$http(API.URL + '/api/news/create');
            },

            update: function (id, data) {
                return data.$http(API.URL + '/api/news/update/' + id);
            }
        }
    }

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .controller('NewsController', NewsController);

    /** @ngInject */
    function NewsController(News, SweetAlert, $state) {
        var vm = this;
        var stateMethod = $state.current.method;

        // List
        if (stateMethod == 'list') {
            vm.news = [];

            News.getNewsList().success(function (data) {
                vm.news = data;
                News.data = data;
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }

        // Update
        if (stateMethod == 'update') {
            News.getOne($state.params.id).success(function (data) {
                vm.news = data;
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        }

        // Create
        if (stateMethod == 'create') {
            vm.news = {};
        }


        vm.save = function () {
            var action = (stateMethod == 'update') ? News.update($state.params.id, vm.news) : News.create(vm.news);

            action.success(function (news) {
                SweetAlert.swal({
                    title: 'Success',
                    text: 'Data successfully saved',
                    timer: 1000,
                    showConfirmButton: false,
                    type: 'success'
                });

                $state.go('user.news');

            }).error(function (err) {
                console.log(err);
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        };

        vm.destroy = function (id) {
            News.destroyNews(id).success(function () {
                vm.news = _.reject(vm.news, function (news) {
                    return id == news.id;
                });
            }).error(function (err) {
                SweetAlert.swal(err.error, err.summary, 'error');
            });
        };
    }


})();

