(function () {
    'use strict';

    angular
        .module('donna')
        .directive('onEnter', onEnter);

    /** @ngInject */
    function onEnter() {
        function link(scope, element, attrs) {
            element.bind("keydown keypress", function (event) {
                if (event.which === 13) {

                    scope.$apply(function () {
                        //scope.$eval(attrs.onEnter);
                        scope.onEnter();
                        if (scope.enterClean) scope.model = '';
                    });

                    event.preventDefault();
                }
            });
        }

        return {
            link: link,
            scope: {
                enterClean: '@',
                onEnter: '&',
                model: '=ngModel'
            }
        }
    }

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .controller('ChatController', ChatController);

    /** @ngInject */
    function ChatController(SweetAlert, LocalService, Chat, $rootScope) {
        var vm = this;

        var token = angular.fromJson(LocalService.get('auth_token')).token;

        Chat.emit("get", {url: "/api/message/connect", data: {token: token}}, function (data) {

            if (data.statusCode === 401 || data.statusCode === 403) {
                LocalService.unset('auth_token');
                $window.location.reload();
            }

            if (data.statusCode != 200) return SweetAlert.swal('Chat error', data.body.summary, 'error');

            $rootScope.messages = data.body.messages;
            $rootScope.totalChatUsers = data.body.total;
        });


        // Emit
        vm.send = function (message) {
            if (message && message.text) {
                Chat.emit('post', {
                    url: '/api/message/create',
                    data: {token: token, text: message.text}
                }, function (data) {
                    if (data.statusCode != 200) return SweetAlert.swal('Chat error', data.body.error, 'error');
                });
            }
        };

        vm.like = function (message) {
            Chat.emit('post', {
                url: '/api/message/like/' + message.id,
                data: {token: token}
            }, function (data) {
                if (data.statusCode != 200) return SweetAlert.swal('Chat error', data.body.error, 'error');
            });
        };

        vm.report = function (message) {
            Chat.emit('post', {
                url: '/api/message/report/' + message.id,
                data: {token: token}
            }, function (data) {
                if (data.statusCode != 200) return SweetAlert.swal('Chat error', data.body.error, 'error');
            });
        }

    }

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .directive('uploadForm', uploadForm)
        .directive('uploadFile', uploadFile);


    function uploadForm($http) {
        function link(scope, element, attrs) {
            element.bind('submit', function (e) {
                var fd = new FormData();

                angular.forEach(scope.model, function (modelValue, modelKey) {
                    if (typeof(modelValue) !== 'object') return fd.append(modelKey, modelValue);

                    angular.forEach(modelValue, function (instanceValue, instanceKey) {
                        fd.append(modelKey, instanceValue);
                    });
                });

                scope.model.$http = function (url, options) {
                    if(!url) throw(new Error("You must pass url to directive $http"));

                    var defaults = {
                        url: url,
                        method: 'POST',
                        data: fd,
                        headers: {'Content-Type': undefined}
                    };

                    options = angular.extend({}, defaults, options);

                    return $http(options);
                }
            });
        }

        return {
            link: link,
            restrict: 'A',
            scope: {
                model: '=uploadForm'
            }
        }
    }

    /** @ngInject */
    function uploadFile() {
        function link(scope, element, attrs) {
            element.bind('change', function (e) {
                scope.$apply(function () {
                    scope.model = e.target.files;
                })
            });
        }

        return {
            link: link,
            restrict: 'A',
            scope: {
                model: '=ngModel'
            }
        }
    }

})();

(function () {
    'use strict';

    angular
        .module('donna')
        .controller('MainController', MainController);

    /** @ngInject */
    function MainController(Chat, $rootScope, SweetAlert, $state) {
        var vm = this;

        $rootScope.messages = [];
        vm.chatConnected = false;

        // Events
        Chat.on('connect', function () {
            console.log("KONEKTO!");
            vm.chatConnected = true;
        });

        Chat.on('disconnect', function () {
            console.log("UMIREM");

            vm.chatConnected = false;
            SweetAlert.swal('Chat error', 'Lost connection!', 'error');
            return $state.go('user.dashboard');
        });

        Chat.on("message", function (data) {
            switch (data.verb) {
                case 'created':
                    if (data.statusCode && data.statusCode != 200) return SweetAlert.swal('Chat error', data.body.summary, 'error');
                    $rootScope.messages.push(data.data);
                    break;
                case 'updated':
                    var msg = _.findWhere($rootScope.messages, {id: data.id});
                    if (msg) {
                        msg.likesNum = data.data.likesNum;
                        msg.reportsNum = data.data.reportsNum;
                    }

                    break;
            }
        });

        Chat.on("newUser", function (data) {
            $rootScope.totalChatUsers = data.total;
            $rootScope.messages.push({event: 'connected', user: data.user});
        });

        Chat.on('userDisconnected', function (data) {
            $rootScope.totalChatUsers = data.total;
            $rootScope.messages.push({event: 'disconnected', user: data.user});
        });

    }
})();

